@import"https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.4.0/css/all.min.css";:root{--panel-bg-dark: rgba(30, 26, 38, 0.82);--panel-bg-light: rgba(255, 252, 253, 0.9);--panel-border-dark: rgba(255, 192, 203, 0.14);--panel-border-light: rgba(219, 112, 147, 0.18);--text-main-dark: #e9dfe6;--text-main-light: #5a3a4a;--text-sub-dark: #b097b3;--text-sub-light: #8a6a8a;--accent: #e8a9c2}body{font-family:"Noto Serif SC","Source Han Serif CN",serif;font-weight:600;font-size:var(--st-font-size, 14px);background:rgba(0,0,0,0);color:var(--text-main-dark);margin:0;padding:10px}body.theme-dark{color:var(--text-main-dark)}body.theme-light{color:var(--text-main-light)}.th-panel{border-radius:18px;border:1px solid var(--panel-border-dark);background:var(--panel-bg-dark);padding:12px;display:grid;gap:12px;contain:content}body.theme-light .th-panel{background:var(--panel-bg-light);border-color:var(--panel-border-light)}.th-header{display:flex;align-items:center;justify-content:space-between;gap:10px}.th-title{font-weight:700;display:flex;align-items:center;gap:8px}.th-title__dot{width:8px;height:8px;border-radius:50%;background:var(--accent);display:inline-block}.th-tools{display:flex;align-items:center;gap:6px}.th-btn{border:1px solid var(--panel-border-dark);background:rgba(0,0,0,0);color:inherit;border-radius:999px;padding:4px 10px;font-size:.85em;cursor:pointer}body.theme-light .th-btn{border-color:var(--panel-border-light)}.th-btn.active{background:var(--accent);color:#fff;border-color:rgba(0,0,0,0)}.th-card{border:1px solid var(--panel-border-dark);border-radius:14px;padding:10px 12px;background:hsla(0,0%,100%,.04)}body.theme-light .th-card{border-color:var(--panel-border-light);background:rgba(0,0,0,.03)}.th-card__header{display:flex;align-items:center;gap:10px;cursor:default}.th-card--collapsible .th-card__header{cursor:pointer}.th-card__title{font-weight:700}.th-icon-btn{margin-left:auto;border:0;background:rgba(0,0,0,0);color:inherit;cursor:pointer}.th-badge{margin-left:auto;font-size:.8em;padding:2px 8px;border-radius:999px;background:rgba(232,169,194,.22)}.th-chevron{margin-left:6px;transition:transform .2s ease}.th-card--collapsible.collapsed .th-card__body{display:none}.th-card--collapsible.collapsed .th-chevron{transform:rotate(-90deg)}.th-card__body{margin-top:8px;line-height:1.6;word-break:break-word;white-space:pre-wrap}.th-muted{color:var(--text-sub-dark);font-style:italic}body.theme-light .th-muted{color:var(--text-sub-light)}.th-memory-list{display:grid;gap:8px}.th-memory-item{border:1px dashed var(--panel-border-dark);border-radius:12px;padding:8px 10px;background:rgba(0,0,0,.04)}body.theme-light .th-memory-item{border-color:var(--panel-border-light);background:rgba(0,0,0,.02)}.th-memory-item__id{font-size:.85em;font-weight:700;color:var(--accent);margin-bottom:4px}.th-memory-item__title{font-weight:700;margin-bottom:4px}.th-memory-item__body{font-size:.95em}@media(max-width: 480px){body{padding:6px}}